import React, {Component} from 'react';
import { Typography } from 'antd';
import { Button } from 'antd';
import {Container, Row, Col} from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'antd/dist/antd.css';
import './../../css/Hero.css';
const { Title, Paragraph, Text } = Typography;

class Hero extends Component {
  render () {
    return (
      <Container fluid className="Hero">
        <div className="Mask">
          <Row lg={1} className="Hero-row">
           <Col className="Hero-col-top">
             <Typography>
               <Title level={5}>
                 <div className="Text-white">
                   ¿Sabías que según la OCDE...?
                 </div>
               </Title>
             </Typography>
           </Col>
           <Col className="Hero-col">
             <Typography>
              <Title level={2}>
                <div className="Text-white">
                  El 42% de los chilenos carece
                  en herramientas básicas de digitalización
                </div>
              </Title>
             </Typography>
           </Col>
           <Col className="Hero-col">
            <Typography>
             <Paragraph>
              <Text className="Text-white">Este dato fué antes del Covid, sólo imaginense como ha sido
              la aceleración en adaptarse a las nuevas tecnologías genera
              la evidencia de una gran brecha tecnológica</Text>
             </Paragraph>
            </Typography>
           </Col>
           <Col className="Hero-col">
             <Button type="primary">QUIERO CAPACITARME</Button>
           </Col>
          </Row>
        </div>
      </Container>
    );
  }
}

export default Hero;
