import React, {Component} from 'react';
import { Typography } from 'antd';
import {Container, Row, Col} from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'antd/dist/antd.css';
import info1 from './../../img/senior-sonrisa.jpg'
import info2 from './../../img/brecha-digital.jpg'
const { Title, Paragraph, Text } = Typography;

class Info extends Component {
  render () {
    return (
      <Container fluid>
        <Row>
          <Col>
            <Typography>
              <Title level={2}>
                <div className="text-center p-5">
                  ¿Qué implica capacitarse digitalmente?
                </div>
              </Title>
            </Typography>
          </Col>
        </Row>
        <Row className="mx-auto p-5" lg={2}>
          <Col lg={6}>
            <Typography>
              <Title level={3}>
                <div className="text-left mb-5">
                  Adaptarse a nuevas tecnologias
                </div>
              </Title>

              <Paragraph>
                <Text>
                  <div className="text-justify">
                    Vivimos en un mundo globalizado en constante cambio. Los tiempos
                    de crisis son complejos pero podemos obtener la oportunidad de salir
                    adelante, preparate para el futuro con la experiencia ZENIOR.
                  </div>
                </Text>
              </Paragraph>
            </Typography>
          </Col>
          <Col log={5}>
                <div>
                  <img
                    alt="capacitacion"
                    src={info1}
                    width="400"
                    height="225"
                    className="d-inline-block align-top"
                  />
                </div>
          </Col>
        </Row>
        <Row className="mx-auto p-5"  lg={2}>
          <Col log={6}>
                <div>
                  <img
                    alt="capacitacion"
                    src={info2}
                    width="400"
                    height="225"
                    className="d-inline-block align-top"
                  />
                </div>
          </Col>
          <Col lg={5}>
            <Typography>
              <Title level={3}>
                <div className="text-right mb-5">
                  Romper la brecha digital
                </div>
              </Title>
              <Text>
                <div className="text-justify">
                  Brindamos una experiencia en poder acortar
                  esta brecha digital, para que TÚ! puedas
                  empoderarte y recapacitar  tus habilidades
                  digitales.
                </div>
              </Text>
            </Typography>
          </Col>
        </Row>
      </Container>
    );
  }
}

export default Info;
