import React, {Component} from 'react';
import {Navbar, Nav} from 'react-bootstrap';
import logo from './../img/zenior-logo-blanco.png'
import 'bootstrap/dist/css/bootstrap.min.css';

class Header extends Component{
  render () {
    return (
     <div className="Header">
       <Navbar bg="dark" variant="dark">
         <Navbar.Brand href="#home">
           <img
             alt="ZENIOR"
             src={logo}
             width="100"
             height="100"
             className="d-inline-block align-top"
           />
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
         <Navbar.Collapse id="basic-navbar-nav">
           <Nav className="mr-auto">
             <Nav.Link href="#home">SENIORS</Nav.Link>
             <Nav.Link href="#link">EMPRESAS / INSTITUCIONES</Nav.Link>
             <Nav.Link href="#link">¿QUIENES SOMOS?</Nav.Link>
             <Nav.Link href="#link">BLOG</Nav.Link>
             <Nav.Link href="#link">CONTACTO</Nav.Link>
             <Nav.Link className="bg-white text-primary" href="#link">INGRESAR AL CURSO</Nav.Link>
           </Nav>
         </Navbar.Collapse>
       </Navbar>
     </div>
    );
  }
}

export default Header;
